const {
	excludeFields,
	excludeTypes,
	UTMs,
	validateLabels,
	COOKIES,
	ignoreInputs
} = require("../values");

const { getCookie } = require("./cookiesServices");
const { hasItem } = require("./functions");
const getLocationDetails = require("./getLocationDetails");
const getUserData = require("./getUserData");
const sentryService = require("./sentryService");

async function dataToSubmit(form) {
	try {
		const labels = findLabelForControl(form);
		let data = getFormFields(form, labels);
		if (!data.valid) throw new Error("Data Not Valid");
		let myData = {};
		if (form.id) myData.form_id = form.id;
		if (form.name) myData.form_name = form.name;
		const uid = getCookie("_jsuid") ? getCookie("_jsuid") : false;
		if (uid) {
			myData.uid = uid;
		}
		myData.source_url = getTheUrl();
		myData.referrer = getCookie(COOKIES.REFERRER);
		myData.source = getCookie(COOKIES.SOURCE);
		myData.UTMs = getUtms();
		myData.user_location = await getLocationDetails();
		myData.user_data = getUserData();
		myData.data = data.responses;
		return myData;
	} catch (error) {
		console.log('error', error);
		sentryService.reportEvent(new Error(error));
	}
}

function getUtms() {
	let myUtms = {};
	for (const utm of UTMs) {
		if (getCookie(COOKIES.CHECK + "_" + utm))
			myUtms[utm] = getCookie(COOKIES.CHECK + "_" + utm);
	}
	return myUtms;
}

function getTheUrl() {
	return window.location.href;
}

function getFormFields(form, labels) {
	let valid = false;
	const exlcidedTypes = getExluddedFields(form);
	// get for fields by looping through manually
	let values = getFormFieldsManually(form, labels, exlcidedTypes);
	valid = isFormvalid(form);
	return { valid, responses: values };
}

function getFormFieldsManually(form, labels, exlcidedTypes) {
	let responses = {};
	let checkboxes = {};
	let radios = {};
	for (var i = 0; i < form.elements.length; i++) {
		let element = form.elements[i];
		let name = getFieldName(element, labels);
		if (name && ignoreInputs.indexOf(element.type) < 0) {
			responses[name] = element.value;
		}
		checkboxes = populateCheckboxes(element, checkboxes, name);
		radios = populateRadios(element, radios, name);
	}
	// remove passwords or other fields that need to ignore
	exlcidedTypes.forEach(field => {
		delete responses[field];
	});
	// replace checkboxes values from responses, use array of selected values found in "checkboxes" object
	responses = swapValues(responses, checkboxes);
	responses = swapValues(responses, radios);
	return responses;
}

function populateCheckboxes(element, checkboxes, name) {
	if (element.type === 'checkbox') {
		if (checkboxes[name]) {
			if (element.checked) {
				checkboxes[name].push(element.value);
			}
		} else {
			checkboxes[name] = [];
			if (element.checked) {
				checkboxes[name].push(element.value);
			}
		}
	}
	return checkboxes;
}

function populateRadios(element, radios, name) {
	if (element.type === 'radio') {
		if (element.checked) {
			radios[name] = element.value;
		}
	}
	return radios;
}

function swapValues(responses, swapWith) {
	let keys = Object.keys(swapWith);
	for (var i = 0; i < keys.length; i++) {
		let key = keys[i];
		let val = swapWith[key];
		if (Array.isArray(val)) {
			val = val.toString();
		}
		responses[key] = val;
	}
	return responses;
}

function getFieldName(element, labels) {
	let name = labels[element.id];
	if (!name) name = element.placeholder;
	if (!name) {
		name = element.name ? element.name : element.id ? element.id : "n/a";
	}
	return name;
}

// get a list of fields that we need to exclude
function getExluddedFields(form) {
	try {
		let excludedList = Array.from(form.elements).filter(f => {
			return hasItem(excludeTypes, f.type.toLowerCase()) || hasItem(excludeFields, f.id);
		}).map((x) => x.name || x.type || x.value);
		return  [...new Set(excludedList)]; // filter dups
	} catch(err) {
		sentryService.reportEvent(new Error(err));
		return [];
	}
}

function findLabelForControl(form) {
	let labels = {};
	let pageLabels = form.getElementsByTagName("label");
	if (pageLabels) {
		for (var i = 0; i < pageLabels.length; i++) {
			let label = pageLabels[i];
			let id = label.htmlFor;
			if (id) {
				labels[id] = label.textContent.trim();
				if (labels[id]) {
					labels[id] = labels[id].replace(/[*]/g,"");
				}
			}
		}
	}
	return labels;
}

function validateEmail(emailAdress) {
	let regexEmail = /^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,3})+$/;
	if (emailAdress.match(regexEmail)) {
		return true;
	} else {
		return false;
	}
}

// Identify if the form has useful lead info i.e Phone# or Email.. 
function isFormvalid(form) {
	let valid = false;
	for (var i = 0; i < form.elements.length; i++) {
		let element = form.elements[i];
		if (validateElement(element)) {
			return true; // break out of loop because we detected that the form has lead data
		}
	}
	return valid;
}

function validateElement(element) {
	// we check if the value of the field is an email or a phone number
	if (validateEmail(element.value)) return true;
	//if (check_for_phone(element.value)) return true;

	const elemName = element.name.toLowerCase();
	const elemType = element.type.toLowerCase();
	const elemid = element.id.toLowerCase();
	if (validateLabels.indexOf(elemName) > -1
		|| validateLabels.indexOf(elemType) > -1
		|| validateLabels.indexOf(elemid) > -1
	) {
		return true;
	}
	return false;
}

function check_for_phone(nbr) {
	var Regex = /\b[\+]?[(]?[0-9]{2,6}[)]?[-\s\.]?[-\s\/\.0-9]{3,15}\b/m;
	if (nbr.length >= 10) return Regex.test(nbr);
	return false;
}

module.exports = dataToSubmit;
